import ComponentError from '../helpers/component-error'

const COD_FLOW = document.getElementById('inpCodFlow')
const COD_FLOW_EXECUTE = document.getElementById('inpCodFlowExecute')
const COD_FLOW_EXECUTE_MASTER = document.getElementById('inpCodFlowExecuteMaster')
const COD_FLOW_EXECUTE_TASK = document.getElementById('inpCodFlowExecuteTask')
const COD_FLOW_EXECUTE_TASK_POSITION = document.getElementById('inpCodFlowExecuteTaskPosition')
const COD_FLOW_EXECUTE_C = document.getElementById('inpCodFlowExecuteC')
const ASSOCIATE_TO_COD_FLOW = document.getElementById('inpAssociateToCodFlow')
const ASSOCIATE_TO_COD_FLOW_EXECUTE = document.getElementById('inpAssociateToCodFlowExecute')
const COD_TASK = document.getElementById('inpCodFlowElement')
const TASK_ALIAS = document.getElementById('inpDsFlowElementAlias')
const IS_SIMULATION = document.getElementById('inpStSimulation')
const USER_TOKEN = document.getElementById('inpToken')

const OrquestraData = {
  codFlow: getInputValue(COD_FLOW),
  codFlowExecute: getInputValue(COD_FLOW_EXECUTE),
  codFlowExecuteMaster: getInputValue(COD_FLOW_EXECUTE_MASTER),
  codFlowExecuteTask: getInputValue(COD_FLOW_EXECUTE_TASK),
  codFlowExecuteTaskPosition: getInputValue(COD_FLOW_EXECUTE_TASK_POSITION),
  codFlowExecuteC: getInputValue(COD_FLOW_EXECUTE_C),
  associateToCodFlow: getInputValue(ASSOCIATE_TO_COD_FLOW),
  associateToCodFlowExecute: getInputValue(ASSOCIATE_TO_COD_FLOW_EXECUTE),
  codTask: getInputValue(COD_TASK),
  taskAlias: getInputValue(TASK_ALIAS),
  isSimulation: getInputValue(IS_SIMULATION),
  userToken: getInputValue(USER_TOKEN)
}

const params = {
  name: 'Form',
  container: '.form-group',
  hideClass: 'u-hidden'
}

/**
 * 💡 Helper Functions
 */
function getInputValue (inputValue) {
  return inputValue
    ? inputValue.value
    : null
}

/**
 * 🔑 Public Methods
 */
function on (target, callback) {
  if (typeof target !== 'string' && !Array.isArray(target)) {
    throw new ComponentError({
      name: params.name,
      message:
      `Erro ao criar o evento 'on', \n${target} não é um parâmetro válido. Você deve utilizar como target somente strings ou arrays de strings.`,
      type: 'invalid_target_for_on_event'
    })
  }

  if (typeof callback !== 'function') {
    throw new ComponentError({
      name: params.name,
      message:
      `Erro ao criar o evento 'on', \n${callback} não é um parâmetro válido. Você deve utilizar como callback somente funções.`,
      type: 'invalid_callback_for_on_event'
    })
  }

  const targets = typeof target === 'string'
    ? [target]
    : target

  if (targets.includes(OrquestraData.taskAlias)) {
    callback(OrquestraData.taskAlias)
  }
}

function conditionals (context = document, helperClass = 'u-hidden') {
  context
    .querySelectorAll('[data-if]')
    .forEach(element => {
      const currentAlias = element.dataset.if
        .replace(/[\s[\]]/g, '') // remove white spaces and brakets
        .split(',') // splits if it's a list, using comma

      if (currentAlias.includes(OrquestraData.taskAlias)) {
        element.classList.remove(helperClass)
      } else {
        element.classList.add(helperClass)
      }
    })
}

function getField (fieldId, options = { alwaysArray: false }) {
  const fields = document.querySelectorAll(`[xname="inp${fieldId}"]`)

  if (!fields.length) {
    return null
  }

  if (options.alwaysArray) {
    return [...fields]
  }

  return fields.length > 1
    ? [...fields]
    : fields[0]
}

function clearField (fieldId) {
  const changeEvent = new Event('change')
  const fields = getField(fieldId, { alwaysArray: true })

  fields.forEach(field => {
    const fieldType = field.type

    if (['text', 'textarea', 'select-one', 'hidden'].includes(fieldType)) {
      field.value = ''
    } else {
      field.checked = false
    }

    field.dispatchEvent(changeEvent)
  })
}

function addRequired (fieldId) {
  const fields = getField(fieldId, { alwaysArray: true })

  if (!fields) {
    return console.error(`[Form] Unable to add required. Field reference ${fieldId} not found`)
  }

  fields.forEach(field => {
    field.setAttribute('required', 'S')
    field.removeAttribute('data-was-required')
  })
}

function removeRequired (fieldId) {
  const fields = getField(fieldId, { alwaysArray: true })

  if (!fields) {
    return console.error(`[Form] Unable to remove required. Field reference ${fieldId} not found`)
  }

  fields.forEach(field => {
    field.setAttribute('required', 'N')
    field.setAttribute('data-was-required', true)
  })
}

function showField (fieldId, container = params.container) {
  const fields = getField(fieldId, { alwaysArray: true })
  const $container = fields[0].closest(container)
  const isRequired = fields
    .filter(field => field.getAttribute('data-was-required')).length > 0

  $container.classList.remove(params.hideClass)

  if (isRequired) {
    addRequired(fieldId)
  }
}

function hideField (fieldId, container = params.container) {
  const fields = getField(fieldId, { alwaysArray: true })
  const $container = fields[0].closest(container)
  const isRequired = fields
    .filter(field => field.getAttribute('required') === 'S').length > 0

  $container.classList.add(params.hideClass)
  clearField(fieldId)

  if (isRequired) {
    removeRequired(fieldId)
  }
}

function getFieldValue (fieldId) {
  const fields = getField(fieldId, { alwaysArray: true })
  const fieldType = fields[0].type

  if (['text', 'textarea', 'select-one', 'hidden'].includes(fieldType)) {
    return fields[0].value
  }

  if (fieldType === 'radio') {
    const checkedField = fields.find(field => field.checked)

    return checkedField
      ? checkedField.value
      : null
  }

  if (fieldType === 'checkbox') {
    const checkedFields = fields
      .filter(field => field.checked)
      .map(field => field.value)

    return checkedFields.length
      ? checkedFields
      : null
  }

  return null
}

function handler ({ target, events }, callback) {
  const fields = getField(target, { alwaysArray: true })
  const arrEvents = typeof events === 'string'
    ? [events]
    : events

  if (!callback) {
    return console.error('[Form] Unable to attach handler. You must inform a callback param')
  }

  fields.forEach(field => {
    arrEvents.forEach(event => {
      field.addEventListener(event, resolveCallbacks)
    })
  })

  resolveCallbacks()

  function resolveCallbacks () {
    const values = getFieldValue(target)
    callback(values)
  }
}

/**
 * 🔑 Public API
 */
export default {
  ...OrquestraData,
  on,
  conditionals,
  getField,
  getFieldValue,
  clearField,
  addRequired,
  removeRequired,
  showField,
  hideField,
  handler
}
