import ComponentError from '../helpers/component-error'

const defaults = {
  btnRemoveRef: '[data-vtable-remove]',
  btnEditRef: '[data-vtable-edit]'
}

export default function VirtualTable (params) {
  params = {
    ...defaults,
    ...params,
    // @todo avaliar uso ou remover
    rowRef: 'data-vtable',
    name: 'VirtualTable'
  }

  const {
    table,
    btnInsert
  } = getOrquestraTable(params)

  const state = {
    rows: [],
    props: params.props
  }

  const instance = {
    table,
    btnInsert,
    vtable: params.vtable,
    mount,
    append,
    remove,
    replace,
    clear
  }

  unwireTableControls()

  instance.vtable._vtable = instance

  return instance

  /**
   * 🔒 Private Methods
   */
  function unwireTableControls () {
    [
      ...instance.table.querySelectorAll('.btn-delete-mv'),
      instance.btnInsert
    ]
      .forEach(btn => btn
        ? btn.removeAttribute('onclick')
        : ''
      )
  }

  function setRows () {
    state.rows = [
      ...instance.table.querySelectorAll('tr:not(.header)')
    ].filter(removeEmptyRow)

    // state.rows = [
    //   ...instance.table.querySelectorAll('tr:not(.header)')
    // ]
    //   .map(getRowData)
    //   .filter(removeEmptyRow)
  }

  function getRowData (row) {
    return [
      ...row.querySelectorAll('[xname]')
    ].reduce((fields, field) => {
      const id = field.getAttribute('xname').substring(3)

      return {
        ...fields,
        [id]: field.value
      }
      // const value = field.value === ''
      //   ? null
      //   : field.value

      // const isSelect = field.tagName.toLowerCase() === 'select'

      // const option = isSelect
      //   ? field.querySelector(`option[value="${value}"]`)
      //   : null

      // const text = isSelect && option
      //   ? field.querySelector(`option[value="${value}"]`).textContent
      //   : field.value

      // fields[id] = {
      //   id,
      //   value,
      //   text
      // }
      // return fields
    }, {})
  }

  function removeEmptyRow (row, rowIndex) {
    const rowData = getRowData(row)
    const isEmpty = Object.values(rowData)
      .every(field => !field)

    if (isEmpty && rowIndex > 0) {
      getTableRow(rowIndex).remove()
    }

    return !isEmpty
  }

  // @todo avaliar necessidade...
  // function isTableRowEmpty (row) {
  //   return [...row.querySelectorAll('[xname]')]
  //     .every(field => !field.value)
  // }

  function getVRow (index) {
    return instance.vtable.children[index]
  }

  function getTableRow (index) {
    return instance.table
      .querySelector(`tr:nth-child(${index + 2})`)
  }

  function addRowTriggers (vRow) {
    vRow.querySelectorAll(params.btnRemoveRef)
      .forEach(btn => {
        btn.addEventListener('click', e =>
          defaultTrigger(e, vRow, remove)
        )
      })

    vRow.querySelectorAll(params.btnEditRef)
      .forEach(btn => {
        btn.addEventListener('click', e =>
          defaultTrigger(e, vRow, params.onEdit)
        )
      })
  }

  function defaultTrigger (e, vRow, callback) {
    const index = [...instance.vtable.children].indexOf(vRow)

    e.preventDefault()
    e.stopPropagation()

    callback(index, getRowData(state.rows[index]), vRow)
  }

  function addTableRow (data) {
    let row

    if (!state.rows.length) {
      row = getTableRow(0)
    } else {
      InsertNewRow(instance.btnInsert, true)

      row = instance.table
        .querySelector('tr:last-child')
    }

    Object.entries(data)
      .forEach(([id, value]) => {
        const field = row.querySelector(`[xname=inp${id}]`)

        if (field) {
          field.value = value
        }
      })

    state.rows.push(row)
  }

  function addVRow (rowData) {
    const markup = params.template({ ...rowData, ...state.props })

    instance.vtable
      .insertAdjacentHTML('beforeend', markup)

    const vRow = instance.vtable.lastElementChild

    vRow.setAttribute(params.rowRef, '')

    addRowTriggers(vRow)
  }

  function populateTableRow (row, data) {
    Object.entries(data)
      .forEach(([id, value]) => {
        const field = row.querySelector(`[xname=inp${id}]`)

        if (field) {
          field.value = value
        }
      })
  }

  function clearTable () {
    InsertNewRow(instance.btnInsert, true)

    instance.table
      .querySelectorAll('tr:not(.header):not(:last-child')
      .forEach(row => row.remove())
  }

  function resolveRowIndex (row) {
    if (Number.isInteger(row)) {
      return row
    }

    const index = [...instance.vtable.children].indexOf(row)

    return index >= 0
      ? index
      : null
  }

  function handleActionAndEmpty () {
    if (params.empty) {
      state.rows.length
        ? hideElement(params.empty)
        : showElement(params.empty)
    }
    if (params.action) {
      state.rows.length
        ? showElement(params.action)
        : hideElement(params.action)
    }
  }

  /**
   * 🔑 Public Methods
   */
  function mount () {
    instance.vtable.innerHTML = null

    setRows()
    handleActionAndEmpty()

    state.rows
      .map(getRowData)
      .forEach(addVRow)
  }

  function append (rowData) {
    addVRow(rowData)
    addTableRow(rowData)
    handleActionAndEmpty()
  }

  function remove (row) {
    const rowIndex = resolveRowIndex(row)

    if (rowIndex === null) {
      return
    }

    getVRow(rowIndex).remove()

    if (state.rows.length === 1) {
      clearTable()
    } else {
      getTableRow(rowIndex).remove()
    }

    state.rows.splice(rowIndex, 1)
    handleActionAndEmpty()
  }

  function replace (row, rowData) {
    const rowIndex = resolveRowIndex(row)

    if (rowIndex === null) {
      return
    }

    const vRow = getVRow(rowIndex)
    const tableRow = getTableRow(rowIndex)
    const markup = params.template({ ...rowData, ...state.props })

    vRow.insertAdjacentHTML('afterend', markup)
    vRow.remove()

    const newVRow = getVRow(rowIndex)

    newVRow.setAttribute(params.rowRef, '')
    addRowTriggers(newVRow)
    populateTableRow(tableRow, rowData)
  }

  function clear () {
    state.rows = []
    instance.vtable.innerHTML = null

    clearTable()
    handleActionAndEmpty()
  }
}

/**
 * 💡 Helper Functions
 */

function showElement (element) {
  element.classList.remove('u-hidden')
}

function hideElement (element) {
  element.classList.add('u-hidden')
}

function getOrquestraTable ({ table, name }) {
  const tagName = table.tagName.toLowerCase()
  const isMultivalue = table.getAttribute('mult') === 'S'
  const btnInsert = table.querySelector('#BtnInsertNewRow')

  if (tagName !== 'table') {
    throw new ComponentError({
      name,
      message:
      'Erro ao iniciar componente.\nO parâmetro table deve ser uma tabela',
      type: 'table_not_found'
    })
  }

  if (!isMultivalue) {
    throw new ComponentError({
      name,
      message:
      'Erro ao iniciar componente.\nO parâmetro table deve ser uma tabela multivalorada',
      type: 'multivalue_table_not_found'
    })
  }

  return { table, btnInsert }
}
