import RisksList from './modules/risks/risks-list'
import RisksActions from './modules/risks/risks-actions'
import Slider from './components/slider'
import Form from './components/form'

const Report = function () {
  const $refs = {
    slider: document.querySelector('#risk-slider')
  }

  const state = {
    views: {
      plan: 'risk-plan',
      search: 'risk-search'
    },
    emitter: null
  }

  /**
   * 🔑 Public Methods
   */
  function init () {
    state.slider = Slider($refs.slider)

    Form.conditionals()

    mount()
  }

  /**
   * 🔒 Private Methods
   */
  function mount () {
    const props = {
      editable: false,
      removeable: false,
      static: true
    }
    const { emitter } = state

    RisksList.mount({ emitter, props })
    RisksActions.mount({ emitter })

    state.slider.disableTransition()
    state.slider.open(state.views.plan)
  }

  return {
    init
  }
}

document
  .addEventListener('DOMContentLoaded', () => {
    Report().init()
  })
