import RisksService from './api/api.risks'
import Toggler from '../../components/toggler'
import {
  getActionsStats,
  getActionsDetails,
  getShowMoreButton
} from './risks-card'

const RisksActions = function () {
  const $refs = {
    codFlowExecute: document.querySelector('#inpCodFlowExecute')
  }

  const state = {
    risks: null,
    emitter: null,
    codFlowExecute: $refs.codFlowExecute.value
  }

  return {
    mount,
    appendActions
  }

  /**
   * 🔑 Public Methods
   */
  function mount ({ emitter }) {
    state.emitter = emitter

    getRisksActions()
  }

  function appendActions (riskDescription) {
    const { risk, index } = findRiskOnState(riskDescription)

    if (risk) {
      appendActionsOnRisk(risk, index)
    }
  }

  /**
   * 🔒 Private Methods
   */
  function getRisksActions () {
    addLoadingState()

    RisksService.getActions({
      codFlowExecute: state.codFlowExecute
    })
      .then(risks => {
        state.risks = risks
        parseRisksActions(risks)
      })
      .finally(removeLoadingState)
  }

  function getRisksCardCtrls () {
    return document
      .querySelector('#risk-list')
      .querySelectorAll('.card-controls')
  }

  function addLoadingState () {
    getRisksCardCtrls()
      .forEach(cardCtrl => {
        cardCtrl.classList.add('-loading')
      })
  }

  function removeLoadingState () {
    getRisksCardCtrls()
      .forEach(cardCtrl => {
        cardCtrl.classList.remove('-loading')
      })
  }

  function findRiskOnState (riskDescription) {
    const risks = Object.values(state.risks)
    const index = risks.findIndex(({ description }) =>
      description === riskDescription.replace(/[+]/g, ' ')
    )

    if (index === null) {
      return {}
    }

    return {
      index,
      risk: risks[index]
    }
  }

  function parseRisksActions (risks) {
    Object.values(risks)
      .forEach(appendActionsOnRisk)
  }

  function appendActionsOnRisk (risk, index) {
    const riskId = `risk-${index + 1}`
    const card = getRiskCard(risk.description)
    const actions = Object.values(risk.actions)
      .reduce(groupActionsByStatus, {
        done: [],
        cancelled: [],
        pending: [],
        total: 0
      })

    const statsMarkup = getActionsStats(actions)
    const btnMarkup = getShowMoreButton(riskId)
    const statsCol = card.querySelector('[data-risk-stats]')
    const ctrlsCol = card.querySelector('[data-risk-ctrls]')

    statsCol
      .insertAdjacentHTML('beforeend', statsMarkup)

    ctrlsCol
      .insertAdjacentHTML('afterbegin', btnMarkup)

    const detailsMarkup = getActionsDetails(actions, riskId)

    card
      .insertAdjacentHTML('beforeend', detailsMarkup)

    Toggler({ id: riskId })
  }

  function getRiskCard (description) {
    return [...document.querySelectorAll('[data-risk-description]')]
      // Ao salvar no banco o Orquestra remove o caracter `+`
      .find(desc => {
        const cardDescription = desc.textContent
          .replace(/[+]/g, ' ')
          .replace(/(\r\n|\n|\r)/gm, '')

        const apiDescription = description
          .replace(/[+]/g, ' ')
          .replace(/(\r\n|\n|\r)/gm, '')

        return cardDescription === apiDescription
      })
      .closest('.card')
  }

  function groupActionsByStatus (actions, action) {
    if (action.acaoStatus === 'Concluída') {
      actions.done.push(action)
    } else if (action.acaoStatus === 'Cancelada') {
      actions.cancelled.push(action)
    } else {
      actions.pending.push(action)
    }

    actions.total += 1

    return actions
  }
}

export default RisksActions()
