import resolveReference from '../helpers/component-reference'

const defaults = {
  viewRef: '.slider-view',
  activeClass: '-active',
  noTransitionClass: '-unanimated'
}

export default function Slider (reference, params) {
  params = {
    ...defaults,
    ...params,
    name: 'Slider'
  }

  const elements = resolveReference(reference, params.name)

  if (elements instanceof HTMLElement) {
    return createInstance(elements, params)
  }

  return elements
    .map(element => createInstance(element, params))
    .filter(element => element !== undefined)
}

function createInstance (slider, params) {
  const views = slider.querySelectorAll(params.viewRef)

  const state = {
    openedView: null
  }

  const instance = {
    slider,
    views,
    open,
    disableTransition,
    enableTransition
  }

  mount()

  instance.slider._slider = instance

  return instance

  /**
   * 🔒 Private Methods
   */
  function mount () {
    instance.views.forEach(setViewPosition)
  }

  function setViewPosition (view, index) {
    if (index === 0) {
      view.style.left = '100%'
    } else {
      view.style.left = `-${(index - 1) * 100}%`
    }
  }

  function resetActiveViews () {
    instance.views.forEach(view => {
      view.classList.remove(params.activeClass)
      view.style.zIndex = 0
    })
  }

  /**
   * 🔑 Public Methods
   */
  function open (viewName) {
    const view = instance.slider.querySelector(`[data-slider-view=${viewName}]`)

    if (!view) {
      return
    }

    resetActiveViews()

    view.classList.add(params.activeClass)
    view.style.zIndex = 1
    instance.openesdView = view
  }

  function disableTransition () {
    instance.slider
      .classList
      .add(params.noTransitionClass)
  }

  function enableTransition () {
    instance.slider
      .classList
      .remove(params.noTransitionClass)
  }
}
