const ORQUESTRA_DATASOURCE_RISKS = process.env.DATASOURCE_BASE_URL + process.env.DATASOURCE_RISKS
const ORQUESTRA_DATASOURCE_ACTIONS = process.env.ORQUESTRA_BASE_URL + '/api/json/datasource/get/1.0' + process.env.DATASOURCE_ACTIONS

const getRisks = function ({ unitId, year }) {
  const url = new URL(ORQUESTRA_DATASOURCE_RISKS)

  const params = {
    inpunidade: unitId,
    inpano: year
  }

  url.search = new URLSearchParams(params).toString()

  return new Promise((resolve) => {
    setTimeout(() => resolve([]), 600)
  })

  // return fetch(url)
  //   .then(res => res.json())
  //   .then(({ success }) => {
  //     return success.map(({ fields }) => fields)
  //   })
}

const getActions = function ({ codFlowExecute }) {
  const url = new URL(ORQUESTRA_DATASOURCE_ACTIONS)

  const params = {
    inpcodFlowExecuteMaster: codFlowExecute
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    .then(({ success }) =>
      success.reduce(parseData, {})
    )
}

export default {
  getRisks,
  getActions
}

/**
 * 🔒 Private Methods
 */
const parseData = (risks, row) => {
  const { fields, txt } = row

  if (!risks[fields.riskId]) {
    risks[fields.riskId] = {
      actions: {}
    }

    if (fields.id === 'riscoDescricao') {
      risks[fields.riskId].description = fields.value
      return risks
    }
  }

  if (fields.id === 'riscoDescricao') {
    return risks
  }

  if (!risks[fields.riskId].actions[txt]) {
    risks[fields.riskId].actions[txt] = {}
  }

  risks[fields.riskId].actions[txt][fields.id] = fields.value

  return risks
}
