import ComponentError from './component-error'

export default function resolveReference (reference, component) {
  // Any QuerySelector string
  // e.g. 'div > .class'
  if (typeof reference === 'string') {
    let elements

    try {
      elements = [...document.querySelectorAll(reference)]
    } catch (error) {
      throw new ComponentError({
        name: component,
        message:
        `Erro ao inicializar a instância do componente.\n${reference} não é um seletor válido`,
        type: 'reference_queryselector_validation'
      })
    }

    if (!elements.length) {
      throw new ComponentError({
        name: component,
        message:
        `Erro ao inicializar a instância do componente.\nNão foi encontrado nenhum elemento para o seletor ${reference}`,
        type: 'reference_queryselector_empty'
      })
    }

    return elements
  }

  // Single HTML Elements
  // e.g. document.querySelector('div')
  if (reference instanceof HTMLElement) {
    return reference
  }

  // HTML Collection and Node List
  // HTMLCollection: e.g. document.getElementsByClassName('myClass')
  // NodeList: e.g. document.querySelectorAll('div')
  if (reference instanceof HTMLCollection) {
    return [...reference]
  }

  // Array of HTML Elements
  // e.g. [document.querySelector('#a'), document.querySelector('#b')]
  if (Array.isArray(reference)) {
    if (!reference.length) {
      throw new ComponentError({
        name: component,
        message:
        'Erro ao inicializar a instância do componente.\nVocê não pode chamar o construtor com array vazio',
        type: 'reference_array_empty'
      })
    }

    reference.forEach(element => {
      if (!(element instanceof HTMLElement)) {
        throw new ComponentError({
          name: component,
          message:
          'Erro ao inicializar a instância do componente\nVocê não pode chamar o construtor com arrays que possuam elementos diferentes de HTMLElement',
          type: 'reference_array_validation'
        })
      }
    })

    return reference
  }

  throw new ComponentError({
    name: component,
    message:
    'Erro ao inicializar a instância do componente.\nVocê deve chamar o construtor com strings (seletores CSS), HTMLElement, HTMLElement[], HTMLCollection ou NodeList',
    type: 'reference_invalid_type'
  })
}
