const defaults = {
  activeClass: '-open',
  containerDataRef: 'data-toggler',
  triggerDataRef: 'data-toggler-trigger'
}

export default function Toggler (params) {
  params = {
    ...defaults,
    ...params,
    name: 'Toggler'
  }

  const {
    container,
    triggers
  } = getElements(params)

  const instance = {
    container,
    triggers,
    open,
    close,
    toggle
  }

  mount()

  instance.container._toggler = instance

  return instance

  /**
   * 🔒 Private Methods
   */
  function mount () {
    instance.triggers.forEach(addTriggers)
  }

  function addTriggers (trigger) {
    trigger.addEventListener('click', e => {
      e.preventDefault()
      toggle()
    })
  }

  function toggleClass (el) {
    el.classList
      .toggle(params.activeClass)
  }

  function setActiveClass (el) {
    el.classList
      .add(params.activeClass)
  }

  function removeActiveClass (el) {
    el.classList
      .remove(params.activeClass)
  }

  /**
   * 🔑 Public Methods
   */
  function open () {
    setActiveClass(instance.container)

    instance.triggers
      .forEach(setActiveClass)
  }

  function close () {
    removeActiveClass(instance.container)

    instance.triggers
      .forEach(removeActiveClass)
  }

  function toggle () {
    toggleClass(instance.container)

    instance.triggers
      .forEach(toggleClass)
  }
}

/**
 * 💡 Helper Functions
 */
function getElements ({ id, containerDataRef, triggerDataRef }) {
  return {
    container: document.querySelector(`[${containerDataRef}=${id}]`),
    triggers: [...document.querySelectorAll(`[${triggerDataRef}=${id}]`)]
  }
}
