export default function CreateCard (props) {
  props = {
    editable: true,
    removeable: true,
    ...props
  }

  const {
    badge,
    modifier
  } = getBadge(props.riscoTipo)

  return `
<div class="card">
  <div class="card-ribbon -sm ${modifier}"></div>
  <div class="card-row">
    <div class="columns -auto">
      <div class="column">
        ${badge}
      </div>
      <div class="column">
        <label class="form-label">${props.riscoTipo}</label>
        <p data-risk-description>${props.riscoDescricao}</p>
      </div>
    </div>
  </div>
  <div class="card-row">
    <div class="columns">
      <div class="column">
        <label
          class="form-label -inline"
          data-help="(${props.riscoOrigem})"
        >Origem</label>
        <p>${props.riscoDetalhesOrigem}</p>
      </div>
      <div class="column">
        <label class="form-label">Consequência</label>
        <p>${props.riscoConsequencia}</p>
      </div>
    </div>
  </div>
  <div class="card-row">
    <div class="columns -auto">
      <div class="column">
        <span class="form-help">Probabilidade</span>
        ${getArrowByLevel(props.riscoProbabilidade)}
      </div>
      <div class="column">
        <span class="form-help">Impacto</span>
        ${getArrowByLevel(props.riscoImpacto)}
      </div>
      <div class="column -grow">
        <span class="form-help">Nível</span><br>
        <span class="tag -blue">${props.riscoNivel}</span>
      </div>
      <div class="column -gapless">
        <div class="icon-badge -sm -boxed">
          <i class="fas fa-${getIconNameByTreatment(props.riscoTratamento)}"></i>
        </div>
      </div>
      <div class="column">
        <span class="form-help">Tratamento</span>
        <label class="form-label">${props.riscoTratamento}</label>
      </div>
    </div>
  </div>
  ${getCardActions(props)}
</div>
`
}

export function getActionsStats (actions) {
  const percent = {
    done: (actions.done.length * 100 / actions.total).toFixed(3),
    cancelled: (actions.cancelled.length * 100 / actions.total).toFixed(3)
  }

  return `
<div class="barchart">
  <div class="barchart-partial -blue" style="flex-basis: ${percent.done}%;"></div>
  <div class="barchart-partial -grey" style="flex-basis: ${percent.cancelled}%;"></div>
</div>
<div class="barchart-legend">
  <span class="barchart-legend-item">
    <i class="barchart-legend-icon -blue fas fa-check"></i>${actions.done.length}
  </span>
  <span class="barchart-legend-item">
    <i class="barchart-legend-icon -grey fas fa-times"></i>${actions.cancelled.length}
  </span>
  <span class="barchart-legend-item">
    <i class="barchart-legend-icon fas fa-adjust"></i>${actions.pending.length}
  </span>
  <span class="barchart-legend-item u-text-deemphasize">(Total ${actions.total})</span>
</div>
`
}

export function getActionsDetails ({ total, ...actions }, riskId) {
  return `
<div class="card-row -expandable" data-toggler="${riskId}">
  ${Object.entries(actions)
      .map(getActionsDetailsByGroup)
      .join('')
    }
</div>
`
}

function getActionsDetailsByGroup ([group, actions], index) {
  const { name, variant } = getGroupData(group)

  if (!actions.length) {
    return ''
  }

  return `
<div class="columns risk-actions-list">
  <div class="column -col-1-3">
    <span class="bullet ${variant}"><strong>${name}</strong></span>
  </div>
  <div class="column -col-2-3">
    <ul class="stack-list -minimal">${actions.map(getActionsDetailRow).join('')}</ul>
  </div>
</div>
`
}

function getActionsDetailRow (action) {
  return `
<li class="stack risk-action-item">
  <div class="columns -auto">
    <div class="column -grow">
      <h4 class="stack-title">${action.acaoTitulo}</h4>
    </div>
    <div class="column">
      <i class="stack-label-icon fas fa-user"></i>
      <span class="stack-label">${action.acaoResponsavel}</span>
    </div>
    <div class="column">
      <i class="stack-label-icon fas fa-calendar"></i>
      <span class="stack-label">${action.acaoPrazo}</span>
    </div>
  </div>
  <p>
    ${action.acaoDescricao}
  </p>
</li>
`
}

function getGroupData (group) {
  if (group === 'done') {
    return {
      name: 'Concluídas',
      variant: '-blue'
    }
  }
  if (group === 'cancelled') {
    return {
      name: 'Canceladas',
      variant: '-grey'
    }
  }
  if (group === 'pending') {
    return {
      name: 'Pendentes',
      variant: ''
    }
  }

  return null
}

const riskBadge = `
<div class="icon-badge -red -sm">
  <i class="fas fa-fire"></i>
</div>`

const opportunityBadge = `
<div class="icon-badge -green -sm">
  <i class="fas fa-seedling"></i>
</div>`

function getBadge (type) {
  type = type.toLowerCase()

  if (type === 'risco') {
    return {
      badge: riskBadge,
      modifier: '-red'
    }
  }
  if (type === 'oportunidade') {
    return {
      badge: opportunityBadge,
      modifier: '-green'
    }
  }

  return null
}

function getArrowByLevel (level) {
  let icon = null
  const text = level
  level = level.toLowerCase()

  if (level === 'baixo' || level === 'baixa') {
    icon = '<i class="fas fa-long-arrow-alt-down u-color-blue-300"></i>'
  }

  if (level === 'médio' || level === 'média') {
    icon = '<i class="fas fa-minus u-color-grey-500"></i>'
  }

  if (level === 'alto' || level === 'alta') {
    icon = '<i class="fas fa-long-arrow-alt-up u-color-blue-700"></i>'
  }

  return `
  <label class="form-label">
    ${icon} ${text}
  </label>
  `
}

function getIconNameByTreatment (treatment) {
  if (treatment === 'Explorar') {
    return 'mountain'
  }

  if (treatment === 'Mitigar') {
    return 'umbrella'
  }

  if (treatment === 'Monitorar') {
    return 'heartbeat'
  }

  if (treatment === 'Transferir') {
    return 'share'
  }

  if (treatment === 'Aceitar') {
    return 'check-double'
  }

  if (treatment === 'Evitar') {
    return 'exclamation-triangle'
  }

  return null
}

function getCardActions ({ removeable, editable }) {
  return `
<div class="card-controls">
  <div class="columns -auto">
    <div class="column -grow" data-risk-stats>
      ${removeable ? getRemoveButton() : ''}
    </div>
    <div class="column" data-risk-ctrls>
      ${editable ? getEditButton() : ''}
    </div>
  </div>
</div>
  `
}

function getEditButton () {
  return `
<button class="btn btn-link" data-vtable-edit>
  <i class="fas fa-edit btn-icon"></i>
  <span>Editar</span>
</button>
  `
}

function getRemoveButton () {
  return `
<button class="btn btn-link -red" data-vtable-remove>
  <i class="fas fa-trash btn-icon"></i>
  <span>Excluir</span>
</button>
  `
}

export function getShowMoreButton (riskId) {
  return `
<button class="btn btn-link" data-toggler-trigger="${riskId}">
  <span>Ações</span>
  <i class="fas fa-angle-up btn-icon"></i>
</button>
  `
}
