import VirtualTable from '../../components/virtual-table'
import CreateCard from './risks-card'

const RisksList = function () {
  const $refs = {
    return: document.querySelector('#btn-risks-return'),
    table: document.querySelector('#risk-table'),
    vtable: document.querySelector('#risk-list'),
    binds: document.querySelectorAll('[data-text-bind]'),
    emptySearch: document.querySelector('#risks-search-empty'),
    emptyList: document.querySelector('#risks-list-empty'),
    action: document.querySelector('#risk-list-action')
  }

  const state = {
    emitter: null
  }

  return {
    mount,
    populate,
    append,
    clear
  }

  /**
   * 🔑 Public Methods
   */
  function mount ({ emitter, props = {} }) {
    state.emitter = emitter

    state.vTable = VirtualTable({
      props,
      table: $refs.table,
      vtable: $refs.vtable,
      empty: $refs.emptyList,
      action: $refs.action,
      template: CreateCard,
      onEdit: onEditCard
    })

    state.vTable.mount()

    $refs.return
      .addEventListener('click', handleReturn)

    $refs.binds
      .forEach(bindText)

    if (props.static) {
      removeAddButtons()
    }
  }

  function populate (risks) {
    $refs.emptyList
      .classList
      .add('u-hidden')

    if (!risks.length) {
      $refs.emptySearch
        .classList
        .remove('u-hidden')
    } else {
      risks.forEach(append)
    }
  }

  function append (data) {
    // @todo ajustar
    $refs.emptySearch
      .classList
      .add('u-hidden')

    state.vTable.append(data)
  }

  function clear () {
    state.vTable.clear()
  }

  /**
   * 🔒 Private Methods
   */
  function handleReturn (e) {
    e.preventDefault()
    state.emitter.emit('search-return')
  }

  function bindText (label) {
    const id = label.dataset.textBind
    const field = document.querySelector(`[xname=inp${id}]`)

    if (!field) {
      return
    }

    label.textContent = getBindValue(field)

    // Datepicker Orquestra não dispara evento
    // nativo `change`, obrigando uso de jQuery
    $(field).on('change', e => {
      label.textContent = getBindValue(e.target)
    })
  }

  function getBindValue (field) {
    const id = field.getAttribute('xname').substring(3)
    const type = field.tagName.toLowerCase()
    const mask = document.querySelector(`[xid=div${id}]`)
    const fieldFormat = field.dataset.fieldformat
      ? field.dataset.fieldformat.toLowerCase()
      : null

    if (!field.value || field.value === '') {
      return ''
    }

    if (field.type === 'hidden' && fieldFormat === 'select') {
      return mask
        ? mask.textContent
        : ''
    }

    if (type === 'select') {
      return field
        .querySelector(`option[value="${field.value}"]`)
        .textContent
    }

    return field.value
  }

  function removeAddButtons () {
    document.querySelectorAll('[data-drawer-open]')
      .forEach(btn => btn.remove())
  }

  function onEditCard (index, data, vRow) {
    state.emitter.emit('drawer-edit', {
      data,
      ref: vRow,
      onRemove: onRemoveCard,
      onConfirm: onSaveCard
    })
  }

  function onRemoveCard (row) {
    state.vTable.remove(row)
  }

  function onSaveCard (row, data) {
    state.vTable.replace(row, data)
    state.emitter.emit('drawer-edit-save', data)
  }
}

export default RisksList()
